.resume{
    background:#aaa;
    padding: 7rem;
}

.resume-page{
    position:relative;
    z-index:400;
}

.resume-page canvas{
    background:white;
    -webkit-box-shadow:0px 2px 4px rgba(0, 0, 0, 0.5);
            box-shadow:0px 2px 4px rgba(0, 0, 0, 0.5);
    margin: 0 auto !important;
}

.document-controls{
    background: rgba(0, 0, 0, 0.4);
    border-radius:.4rem;
    color:white;
    position:fixed;
    bottom:9rem;
    padding: 2rem;
    right:5%;
    text-align:center;
    width:auto;
    z-index:500;
}

.next-page, 
.previous-page, 
.zoom-in, 
.zoom-out {
    background:none;
    border:0;
    color:white;
    font-size: 3.6rem;
}

.page-number{
    font-size:1.2rem;
    margin-bottom: 1.2rem;
}

.download-resume button{
    background:none;
    border: 1px solid white;
    border-radius:4px;
    color:white;
    margin-top:.8rem;
}

@media screen and (max-width:740px){
    .phone{
        display:inherit;
    }
    .resume{
        padding:0;
        position:relative;
        top:8rem;
    }
    .resume-page{
        margin-bottom:2rem;
    }
    .desktop{
        display:none;
    }
}

@media screen and (min-width: 741px){
    .phone{
        display:none;
    }
    .desktop{
        display:inherit;
    }
}


